<template>
  <div id="app" class="box">
    <Loading v-show="$store.state.bLoading"></Loading>
    <Search v-show="$store.state.bSearch"></Search>
    <Header v-show="$store.state.bNav"></Header>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <Footer v-show="$store.state.bFoot"></Footer>
  </div>
</template>

<script>
import Search from './components/Search'
import Header from './components/Header'
import Footer from './components/Footer'
import Loading from './components/Loading'

import { VIEW_NAV, VIEW_FOOT, VIEW_SEARCH} from './store/types.js'

export default {
  name: 'app',
  components: {
    Search,Header,Footer,Loading,
  },
  methods:{
    checkPath(path){//路由检测
      if(/home|follow|column|shelves|category/.test(path)){
        this.$store.dispatch(VIEW_NAV,true)
        this.$store.dispatch(VIEW_FOOT,true)
        this.$store.dispatch(VIEW_SEARCH,true)
      }
      if(/user|customer|store|carts|User/.test(path)){
        this.$store.dispatch(VIEW_NAV,false)
        this.$store.dispatch(VIEW_FOOT,true)
        this.$store.dispatch(VIEW_SEARCH,false)
      }
      
      if(/Detail|login|reg/.test(path)){
        this.$store.dispatch(VIEW_NAV,false)
        this.$store.dispatch(VIEW_FOOT,false)
        this.$store.dispatch(VIEW_SEARCH,false)

      }
    }
  },
  watch:{
    $route:{
      handler(current,prev){
        this.checkPath(current.path)
      },
      immediate:true,
      deep:true
    }
  }
}
</script>

